body {
  display: flex;
  justify-content: center;
  background-color: #fcfcfc;
  font-family: futura;

}

.App {
  background-color:#ffce6b;
  width: 25rem;
  padding: 1.2rem;
  box-shadow: 2px 2px 2px #000;
  border-radius: 0.2rem;
}

ul {
  list-style: none;
}

.button {
  background-color: #f5f5f5;
  margin: 0 auto;
  width: 5rem;
  height: 2.5rem;
  padding: 0.5rem;
  border-radius: 0.7rem;
  font-size: 1rem;
  font-family: Futura;
  color: #595858;
  margin-left: 9.8rem;
}

.button:hover {
  background-color: #ebedeb;
  color: #000;
  border: 1px solid #000;
  cursor: pointer;
}

.middle-form, .form-all, .form-group {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1.2rem;
  font-size: 1.4rem;
}

.form-control {
  height: 1.4rem;
  width: 22rem;
  font-size: 1.2rem;
  font-family: georgia;
}

.info .expense {
  margin-left: 7.7rem;
  display: flex;
  justify-content: space-around;
  margin-right: 10rem;
  font-size: 1.2rem;
  line-height: 1.1rem;
}

.info .amount {
  margin-left: 7.5rem;
  display: flex;
  justify-content: space-around;
  margin-right: 5rem;
  font-size: 1rem;
  background-color: #fff;
  width: 5rem;
  padding: 0.1rem;
  border-radius: 0.7rem;
  margin-top: 0.2rem;
}

.Edit-button {
  margin-left: 8.3rem;
}

.clear-button:hover, .Edit-button:hover {
  background-color: #ebedeb;
  color: #000;
  border: 1px solid #000;
  cursor: pointer;
  border-radius: 0.2rem;
}

hr {
  margin-right: 12%;
}

.alert {
  background-color: #ffce6b;
  box-shadow: 4px 4px 4px #000;
  color: #000;
}

.button-trash {
  background-color: #f5f5f5;
  margin: 0 auto;
  width: 5rem;
  height: 2.5rem;
  padding: 0.5rem;
  border-radius: 0.7rem;
  font-size: 1rem;
  font-family: Futura;
  color: #595858;
  margin-left: 9.8rem;
}

.button-trash:hover {
  background-color: #ebedeb;
  color: #000;
  border: 1px solid #000;
  cursor: pointer;
  border-radius: 0.6rem;
}

